:root {
  --primary: rgb(156, 51, 83);
}

.NavBar {
  width: 100%;
  height: 70px;
  display: grid;
  grid-template-columns: 10fr 7fr;
  grid-gap: 2px;
  box-shadow: 0 8px 12px -6px rgba(156, 51, 83, 0.281);
  position:fixed;
  background-color: white;
  z-index: 2;
}
.NavBar-leftGrid {
  display: grid;
  height: 70px;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1.2fr 2fr;
  grid-gap: 10px;
  align-items: center;
}


.NavBar-leftGrid img {
  width: 100px;
  margin: 0 25px 0 15px;
}

.NavBar-leftGrid i {
  font-size: 2.4rem;
  color: rgb(156, 51, 83);
  margin-left: 15px;
}

.NavBar-rightGrid {
  display: grid;
  grid-gap: 190px;
}

.nav_options {
  font-size: 1.05rem;
  font-weight: 600;
  text-decoration: none;
  color: black;
  text-align: center;
  padding: 20px 0px;
}

.nav_options:hover {
  opacity: 0.8;
  background: rgba(218, 218, 218, 0.568);
  border-bottom: 1px solid var(--primary);
}

.nav_options_active {
  background: var(--primary);
  font-weight: 600;
  color: rgb(253, 190, 208);
  transition: 0.1s ease-in;
  font-size: 1rem;
  text-decoration: none;
  text-align: center;
  padding: 20px 5px;
}

.NavBar-rightGrid {
  display: grid;
  grid-template-columns: 1fr 4fr 5fr;
  grid-gap:20px;
  margin:0px 0px;
  align-items: center;
}

.StartFundBtn {
  border: none;
  width: 230px;
  height: 45px;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 1px;
  color: #ffff;
  background-color: rgb(156, 51, 83);
  border-radius: 30px;
}
.StartFundBtn:hover {
  background: rgb(150, 48, 80);
  box-shadow: 0 6px 6px -6px black;
  cursor: pointer;
}

.NavBar-rightGrid i {
  font-size: 40px;
  color: rgb(138, 66, 87);
  font-weight: 100;
  margin-left: 100px;
}
.NavBar-rightGrid i:hover {
  color: rgb(102, 32, 54);
  cursor: pointer;
}

.toggle_switch {
  margin-left: 20px;
  width: 75px;
  height: 30px;
  border-radius: 20px;
  background: var(--primary);
  display: flex;
}
.toggle_switch:hover {
  cursor: pointer;
}

.toggle_dial {
  width: 30px;
  height: 30px;
  box-shadow: 0 0 7px grey;
  border-radius: 50%;
  background: white;
  position: relative;
  left: 0;
  transition: linear 0.1s;
}

.toggleText {
  position: relative;
  font-size: 0.9rem;
  text-align: center;
  left: 5px;
  top: 7px;
  color: white;
}

.toggled .toggle_dial {
  left: 45px;
}

.toggled .toggleText {
  top: 7px;
  left: -20px;
}

.ProfileDiv{
  display: grid;
  grid-template-columns: 2fr 3fr;
  align-items: center;
  position: relative;
}
.ProfileDiv div{
  font-size: 1rem;
  margin-left: 40px;
  width:45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  color:black;
  background-color: rgba(207, 207, 207, 0.377);
  color:grey;
  border-radius: 50%;
  text-decoration: none;
}



.ProfileDiv p{
  text-decoration: none;
  font-size: 0.9rem;
  color:grey;
  padding-left:5px;
}
.logOutSpan{
  visibility: hidden;
  width:140px;
  height: 40px;
  top:60px;
  left:40px;
  outline:1px solid var(--primary);
  background-color: white;
  padding: 3px 8px;
  text-align: center;
  position: absolute;
  z-index:3;
  display:flex;
  align-items: center;
  justify-content: center;
}

.logOutBtn{
  border:none;
  border-radius: 20px;
  padding:4px 15px;
  background-color: var(--primary);
  color:rgb(255, 255, 255);
  margin-top: 3px;;
}

.logOutBtn:hover{
  cursor: pointer;
}

.logOutVis{
  visibility: visible;
  width:140px;
  height: 30px;
  top:60px;
  left:40px;
  outline:1px solid var(--primary);
  background-color: white;
  padding: 3px 8px;
  text-align: center;
  position: absolute;
  z-index:3;
}

